import React from 'react';
import {
    Router,
    Redirect,
    Switch,
    Route
} from "react-router-dom";
import './App.css';
import NavBar from './Components/NavBar';
import Footer from './Components/Footer';
import Home from './Pages/Home/Home';
import AboutMe from './Pages/AboutMe/AboutMe';
import NewPost from "./Pages/Posts/NewPost";
import PostPageView from "./Pages/Posts/PostPageView";
import Login from "./Pages/Login/Login";
import {AppBar, Toolbar} from '@material-ui/core';
import Cookies from 'js-cookie';
import {createBrowserHistory} from 'history'
const history = createBrowserHistory();

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state ={
            username: null,
            full_name: null,
            sessionID: Cookies.get('sessionID'),
        };
        this.handler = this.handler.bind(this);
    }

    handler(data){
        this.setState(data);
    }

    render(){

        return (
            <div className="App" >
                <Router history = {history}>
                    <header>
                        <AppBar color="transparent">
                            <Toolbar>
                                <NavBar username={this.state.username} />
                            </Toolbar>
                        </AppBar>
                        <Toolbar id="back-to-top-anchor" />

                    </header>
                    <Switch >
                        <Route path="/AboutMe" component={AboutMe}/>
                        <Route path="/Post/:id" component={PostPageView} />
                        <Route path="/NewPost"><NewPost handler={this.handler} parentState={this.state}/></Route>
                        <Route path="/Login"><Login onLogin={this.onLogin} handler={this.handler} history={history}/></Route>
                        <Route path="/Home" component={Home} />
                        <Redirect from="/" to="/Home" />
                    </Switch>
                </Router>
                <Footer />
            </div>
        );
    }
}



export default App;
