import React from 'react';
import axios from "axios";

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            'data': [],
            'resp': null
        };

    }
    OnChangeUsername = (e)=>{
        this.setState({username: e.target.value});
    }
    OnChangePassword = (e)=> {
        this.setState({password: e.target.value});
    }


    handleSubmit = () => {
        const url = "/login/";

        let data = {
            username: this.state.username,
            password: this.state.password
        };
        axios.post(url, data)
            .then((res) => {
                this.setState({
                    data: [],
                    resp: "sucess"

                });
                this.props.handler({username: this.state.username, full_name: res.data['full_name']});
                this.goBack();
            })
            .catch(() => {
                this.setState({
                    data: [],
                    resp: "error"
                });
            });


    }
    goBack(){
        let history = this.props.history;
        history.goBack();
        console.log("KENN");
    }
    keyPress = (e)=>{
        if(e.keyCode === 13){
            this.handleSubmit();
        }
    }
    render() {
        return (
            <div className="form-group" align="center">
                <div className="form-group  col-md-6">
                    <label htmlFor="inputEmail3" className="col-sm-2 col-form-label"><h1>Login</h1>Demo User:<br />Username: demo<br />Password: demo</label>
                </div>
                <div className="form-group  col-md-6">
                    <div className="col-sm-10">
                        <input type="username" name="username" onKeyDown={this.keyPress} className="form-control" id="inputEmail3" placeholder="Username" onChange={this.OnChangeUsername}/>
                    </div>
                </div>
                <div className="form-group col-md-6">
                    <div className="col-sm-10">
                        <input type="password" className="form-control" onKeyDown={this.keyPress} id="inputPassword3" placeholder="Password"  onChange={this.OnChangePassword}/>
                    </div>
                </div>
                <div className="form-group col-md-6">
                    <div className="col-sm-10">
                        <button type="submit" className="btn btn-primary" onClick={this.handleSubmit}>Sign in
                        </button>
                    </div>
                </div>
            </div>

       );
    }
}

export default Login;